<template>
  <div>
    <img src="../assets/images/nav-pull.jpg" class="btn-nav-pull" v-on:click="show = !show">
    <transition name="fade">
      <div v-if="show" class="nav-pull">
        <img src="../assets/images/nav-pull.jpg" class="btn-nav-pull" v-on:click="show = !show">
        <div class="nav-titles">
          <div class="nav-bar">
            <router-link to="/">Home</router-link>
          </div>
          <div class="nav-bar">
            <router-link to="/login">Login</router-link>
          </div>
          <div class="nav-bar">
            <router-link to="/register">Register</router-link>
          </div>
          <div class="nav-bar">
            <a href @click.prevent="logOut">
              LogOut
            </a>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: "NavPull",
    data() {
      return {
        show: false,
      }
    },
    methods: {
      logOut() {
        this.$store.dispatch('auth/logout');
        this.$router.push('/login');
      }
    }
  }
</script>

<style scoped>
  @import '../assets/css/nav-pull.css';
</style>